@import url("https://fonts.googleapis.com/css2?family=Montserrat&family=Nanum+Gothic&display=swap");
$clr-gray: rgba(black, 0.7);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;

  .totalContainer {
    margin-top: 56px;
    margin-left: 0;
    margin-right: 0;

    .mainImg {
      position: relative;
      background-image: url("../../Images/main.jpg");
      background-size: cover;
      background-position: 50% 50%;
      background-repeat: no-repeat;
      background-attachment: fixed;
      width: 100%;
      height: 600px;

      .mainText {
        position: absolute;
        top: 45%;
        left: 50%;
        transform: translate(-50%, -50%);
        text-align: center;
        font-family: "Montserrat", sans-serif;
        z-index: 2;

        .mainTitle {
          color: white;
          display: flex;
          white-space: nowrap;
          font-size: 5rem;

          span {
            font-weight: 400;
            padding-right: 0.3rem;
          }
          .modern {
            font-weight: 800;
          }
        }
        .mainTag {
          position: relative;
          ul {
            display: flex;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            li {
              background-color: #fff !important;
              border-radius: 1px;
              font-size: 2em;
              display: inline-block;
              padding: 5px 12px 8px;
              margin-left: 7px;
            }
          }
        }
      }
      .imgCover {
        position: absolute;
        height: 100%;
        width: 100%;
        background-color: rgba(0, 0, 0, 0.2);
        z-index: 1;
      }
    }
    .imgContainer {
      width: 100%;
      display: flex;
      position: relative;

      .imgList {
        width: 25%;
        position: relative;
        background: $clr-gray;
        overflow: hidden;

        &::before,
        &::after {
          content: "";
          position: absolute;
          top: 1.25em;
          bottom: 1.25em;
          left: 1.25em;
          right: 1.25em;
        }

        &::before {
          transform: scale(0, 1);
          transition: transform ease-out 250ms;
          border-top: 1px solid white;
          border-bottom: 1px solid white;
        }
        &::after {
          transform: scale(1, 0);
          transition: transform ease-out 250ms;
          border-left: 1px solid white;
          border-right: 1px solid white;
        }

        &:hover::before {
          transform: scale(1.05, 1);
        }
        &:hover::after {
          transform: scale(1, 1.05);
        }

        img {
          margin: 0;
          padding: 0;
          width: 477px;
          position: relative;
          height: 100%;
          object-fit: cover;
          display: block;
          transition: opacity ease-out 250ms;
          opacity: 1;
        }

        p {
          position: absolute;
          // white-space: nowrap;
          text-align: center;
          font-family: "Montserrat", sans-serif;
          font-weight: 600;
          z-index: 400;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          color: white;
          white-space: pre-wrap;
          font-size: 1.5rem;
          opacity: 0;
          transition: opacity ease-out 250ms;
        }
      }
      .imgList:hover {
        img {
          opacity: 0.4;
        }
      }
      .imgList:hover {
        p {
          opacity: 1;
        }
      }
    }
    .lastMainpage {
      position: relative;
      width: 100%;
      height: 750px;

      .explore {
        width: 195px;
        height: 47px;
        margin: 40px auto;

        span {
          display: inline-block;
          margin-right: 10px;
          font-size: 20px;
          text-align: center;

          &:nth-child(2) {
            font-weight: bold;
            font-size: 22px;
          }
        }
      }
      .lastImgs {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 1000px;
        height: 500px;
        display: flex;
        overflow: hidden;

        .imgs {
          margin-left: 20px;
          position: relative;
          overflow: hidden;
          object-fit: cover;

          img {
            margin: 0;
            padding: 0;
            width: 300px;
            position: relative;
            height: 100%;
            object-fit: cover;
            overflow: hidden;
          }

          &:hover {
            transform: scale(1.1);
            transition: 0.2s;
          }

          p {
            position: absolute;
            bottom: 6%;
            left: 50%;
            transform: translateX(-50%);
            white-space: nowrap;
            text-align: center;
            font-family: "Montserrat", sans-serif;
            font-weight: 500;
            background: #fff;
            padding: 5px 12px 7px;
            z-index: 1000;
            border-radius: 1px;
          }
        }
      }
    }
  }
}

@media screen and (min-width: 320px) and(max-width: 768px) {
  body {
    margin-top: 56px;
    margin-left: 0;
    margin-right: 0;

    .mainImg {
      background-image: url("../../Images/main.jpg");
      background-size: cover;
      background-position: 50% 50%;
      background-repeat: no-repeat;
      width: 100%;
      height: 450px;
      position: relative;

      .mainText {
        position: absolute;
        top: 40%;
        left: 50%;
        transform: translate(-50%, -50%);
        text-align: center;
        font-family: "Montserrat", sans-serif;
        z-index: 2;

        .mainTitle {
          color: white;
          display: flex;
          white-space: nowrap;
          position: absolute;
          bottom: 1rem;
          left: -15rem;

          span {
            font-weight: 400;
            padding-right: 2px;
            font-size: 2.3rem;
          }
          .modern {
            font-weight: 900;
          }
        }
        .mainTag {
          position: relative;
          ul {
            display: flex;
            position: absolute;
            left: 40px;
            top: 3rem;
            font-size: 0.6rem;

            li {
              background-color: #fff !important;
              border-radius: 1px;
            }
          }
        }
      }
      .imgCover {
        position: absolute;
        height: 100%;
        width: 100%;
        background-color: rgba(0, 0, 0, 0.2);
        z-index: 1;
      }
    }
    .imgContainer {
      width: 100%;
      display: flex;

      .imgList {
        width: 25%;
        height: 300px;
        display: flex;

        img {
          margin: 0;
          padding: 0;
          width: 200px;
          position: relative;
          height: 100%;
          object-fit: cover;
          overflow: hidden;
        }

        p {
          font-size: 0.2rem;
        }
      }

      .cover {
        display: none;
      }
    }
    .lastMainpage {
      width: 100%;
      height: 450px;

      .explore {
        width: 195px;
        height: 40px;
        margin-left: 230px;

        span {
          display: inline-block;
          margin-right: 10px;
          font-size: 15px;
          text-align: center;

          &:nth-child(2) {
            font-weight: bold;
            font-size: 17px;
          }
        }
      }
    }
  }
}
